@font-face {
  font-family: 'icomoon';
  src: url("https://assets.fathead.com/evolution/fonts/icomoon.eot?fb0hhs");
  src: url("https://assets.fathead.com/evolution/fonts/icomoon.eot?fb0hhs#iefix") format("embedded-opentype"), url("https://assets.fathead.com/evolution/fonts/icomoon.ttf?fb0hhs") format("truetype"), url("https://assets.fathead.com/evolution/fonts/icomoon.woff?fb0hhs") format("woff"), url("https://assets.fathead.com/evolution/fonts/icomoon.svg?fb0hhs#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-cart:before {
  content: "\E900";
}
.icon-facebook:before {
  content: "\E901";
}
.icon-heart:before {
  content: "\E902";
}
.icon-instagram:before {
  content: "\E903";
}
.icon-pinterest:before {
  content: "\E904";
}
.icon-twitter:before {
  content: "\E905";
}

