[v-cloak] {
  display: none;
}
html {
  background-color: #eee5e9;
}
body {
  margin: 0;
  background-color: #eee5e9;
  font-family: "Barlow Semi Condensed", sans-serif;
  font-weight: 300;
}
.center {
  text-align: center;
}
#router-container {
  min-height: calc(100vh - (105px + 40px));
}
.container {
  margin: 5px 15px;
}
@media (max-width: 600px) {
.container {
      margin: 5px 10px;
}
}
.container .content-block {
    border: 2px solid #f0f3f5;
    margin-bottom: 15px;
    padding: 10px 20px;
    background-color: #FFFDF5;
}
.text-input {
