  width: 100%;
}
.button {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 2px;
  text-shadow: 1px 1px 0 #555;
  background-color: #af7056;
  color: white;
  transition: all 0.5s ease;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
}
.button:hover {
    background-color: #8f5943;
}
.button.plain {
    background-color: transparent;
    color: #383836;
    text-shadow: none;
    border: 1px solid #000;
}
.button.plain:hover {
      background-color: transparent;
}
.button.footer {
    background-color: #FFFDF5;
    color: #383836;
    text-shadow: none;
    border: 1px solid #000;
}
.button.footer:hover {
      background-color: #fff3c2;
}
.button.big {
    padding: 15px 40px;
    font-size: 1.5em;
}
.button.small {
    padding: 5px 18px;
    font-size: .8em;
}
.button.tiny {
    padding: 5px 41px;
    font-size: .65em;
}
.button.checkout {
    text-align: center;
    margin: 0 0 20px;
}
.button.red {
    border: 1px solid #ae1d29;
    color: #ae1d29;
}
.button.search-page {
    font-size: 1em !important;
    padding: 0 10px;
    color: #af7056;
    background-color: white;
    border: 1px solid #af7056;
}
.button.search-page:hover {
      color: white;
      background-color: #af7056;
      border: 1px solid #af7056;
}
.button.search-page-expanded {
    font-size: 1em !important;
    padding: 0 10px;
    color: white;
    background-color: #af7056;
    border: 1px solid #af7056;
}
.button.search-page-expanded:hover {
      color: white;
      background-color: #8f5943;
      border: 1px solid #af7056;
}
.slide-right-enter-active {
  transition: all .3s ease;
}
.slide-right-leave-active {
  transition: all .3s ease;
}
.slide-right-enter, .slide-right-leave-to {
  transform: translateX(50px);
  opacity: 0;
}
#app {
  margin: 0;
  padding: 115px 0 0 0;
  position: relative;
  width: 100%;
}
@media (max-width: 600px) {
