  display: inline-block;
  color: #7e7e7e;
}
a {
  cursor: pointer;
}
a.no-style {
    text-decoration: none;
    color: inherit;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  height: 105px;
  border: 15px solid #eee5e9;
  box-sizing: border-box;
  /*    box-shadow: 0px 1px 8px 0px $content-text-color;*/
}
@media (max-width: 600px) {
header {
      border: none;
}
}
header .nav-header-text {
    font-family: "Playfair Display", serif;
    font-size: 30px;
}
@media (max-width: 800px) {
header .nav-header-text {
        font-size: 25px;
}
}
@media (max-width: 600px) {
header .nav-header-text {
        display: none;
}
}
header .nav-header-text-mobile {
    font-family: "Playfair Display", serif;
    font-size: 30px;
    display: none;
}
@media (max-width: 600px) {
header .nav-header-text-mobile {
        display: inherit;
}
}
header .header-cta {
    height: 30px;
    line-height: 30px;
    text-align: center;
    background-color: #af7056;
    color: white;
}
header .nav {
    background-color: #FFFDF5;
    color: #383836;
    text-align: center;
    line-height: 60px;
    height: 60px;
    border: 2px solid #f0f3f5;
}
header .nav .header-right {
      position: absolute;
      right: 20px;
}
header .nav .header-right .underlined {
        border-bottom: 2px solid #702626;
}
header .nav .hamburger {
      width: 45px;
      height: 22px;
      cursor: pointer;
      position: absolute;
      left: 20px;
      margin-top: 18px;
}
@media (max-width: 600px) {
header .nav .hamburger {
          width: 35px;
          height: 18px;
          margin-top: 20px;
}
}
header .nav .bar,
    header .nav .bar:after,
    header .nav .bar:before {
      width: 45px;
      height: 2px;
}
@media (max-width: 600px) {
header .nav .bar,
        header .nav .bar:after,
        header .nav .bar:before {
          width: 35px;
          height: 2px;
}
}
header .nav .bar {
      position: relative;
      transform: translateY(10px);
      transition: all 0ms 300ms;
}
@media (max-width: 600px) {
header .nav .bar {
          transform: translateY(8px);
}
}
header .nav .bar.animate {
        background: rgba(255, 255, 255, 0);
}
header .nav .bar:before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 10px;
      background: #af7056;
      transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
@media (max-width: 600px) {
header .nav .bar:before {
          bottom: 8px;
}
}
header .nav .bar:after {
      content: "";
      position: absolute;
      left: 0;
      top: 10px;
      background: #af7056;
      transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
@media (max-width: 600px) {
header .nav .bar:after {
          top: 8px;
}
}
header .nav .bar.animate:after {
      top: 0;
      transform: rotate(45deg);
      transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
header .nav .bar.animate:before {
      bottom: 0;
      transform: rotate(-45deg);
      transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
header .evolution-wallpaper {
    font-family: "Playfair Display", serif;
    font-weight: 700;
    font-size: 30px;
    color: #702626;
}
header a {
    color: #383836;
    text-decoration: none;
    padding: 5px;
}
header .fade-enter-active, header .fade-leave-active {
    transition: opacity .5s;
}
header .fade-enter, header .fade-leave-to {
    opacity: 0;
}
header .header-heart {
    color: #ae1d29;
    font-size: 1.6em;
}
header .header-heart:hover {
      color: #da2433;
}
@media (max-width: 800px) {
