}
}
@media (max-width: 600px) {
header .favorites-count {
        font-size: .65em;
        width: 18px;
        height: 18px;
        top: 8px;
        left: 18px;
}
}
header .cart-count {
    position: absolute;
    background-color: white;
    color: #383836;
    height: 20px;
    width: 20px;
    line-height: 20px;
    text-align: center;
    border: 1px solid #af7056;
    left: 74px;
    top: 4px;
    border-radius: 25px;
    font-size: .8em;
}
@media (max-width: 800px) {
header .cart-count {
        font-size: .75em;
        left: 20px;
        top: 6px;
}
}
@media (max-width: 600px) {
