/*
 START FILTER SEARCH PAGE
 */
.ais-results {
  display: grid;
  grid-gap: 120px 10px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 210px;
  margin-top: 20px;
